import React, { useState, useEffect } from "react";
import Writer from "./common/writer/writer";
import { getElementDimensions } from "../helpers";
import OneWriter from "./common/writer/onewriter";

function Navbar(props) {
  const scrollTo = (ele) => {
    let { offsetTop } = getElementDimensions(ele);
    let middle = offsetTop - window.innerHeight / 5;
    window.scrollTo({ top: middle > 0 ? middle : 0, behavior: "smooth" });
  };

  const [state, setstate] = useState(
    props.sections.map((x) => {
      return { ...x, hovered: false };
    })
  );

  useEffect(() => {
    let oldState = state;
    setstate(
      props.sections.map((x) => {
        let oldElem = oldState.find((y) => y.id == x.id);
        return { ...x, hovered: oldElem.hovered };
      })
    );
  }, [props.sections]);

  function setHovered(id) {
    setstate(state.map((x) => (x.id == id ? { ...x, hovered: true } : x)));
  }

  function unHoverAll() {
    setstate(state.map((x) => (x.hovered ? { ...x, hovered: false } : x)));
  }

  return (
    <div className="navbar">
      <div className="navbar__list">
        {state.map((section) => (
          <div
            className={`navbar__item ${section.active ? "navbar__item--active" : ""}`}
            onClick={() => scrollTo(section.ref.current)}
            onMouseOverCapture={() => {
              setHovered(section.id);
            }}
            onMouseOutCapture={() => {
              unHoverAll();
            }}
          >
            <div className="item__button">
              <i className={section.icon}></i>
            </div>
            <div className="item__description">
              <OneWriter word={section.name} reset={section.hovered} maxDelay={30} />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Navbar;
